


















import { Component, Vue } from 'vue-property-decorator';

@Component({
  components: {
  },
})
export default class GoToGame extends Vue {

    LOGGING_CLASS_NAME: string = '[GOTOGAME]';

    gameId: string = '';

    navigateToGame() {
        this.$router.push({ path: `/games/${this.gameId}`});
    }
}
